import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import GetStarted from '../components/GetStarted'
import GetStartedImage1 from '../assets/img/content/get_start_1.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/finance.png';

export default class Finance extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		return (
			<div className='container'>
				<Header
					slot={
						<div>
							<div className='largest__text'>
								<div>
									<span className='dark__blue'></span>
									<span className='blue'></span>
								</div>
								<div>
									<span className='blue'></span>
								</div>
							</div>

							<img src={HeaderImage} alt='Header' className='header__image'/>
						</div>
					}
				/>

				<GetStarted
					getStartedData={
						[{
							image: GetStartedImage1,
							headerText:
								<div>
									<span className='dark__blue'>1.</span>
									<span className='blue'></span>
									<span className='dark__blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'></span>,
							bodyText: 
								<span className='dark__blue'>
									<font className='text__bold'></font> <br></br>
									 <br></br>
									 
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<font className='text__bold'></font> <br></br>
									 <br></br>
									<br></br> 
									
								</span>
						},{
							image: GetStartedImage2,
							headerText:
								<div>
									<span className='dark__blue'>2.</span>
									<span className='blue'></span>
									<span className='dark__blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'></span>,
							bodyText: 
								<span className='dark__blue'>
									<br></br>
									   <br></br>
									<br></br>
									<font className='text__bold'></font>, <font className='text__bold'></font>, <font className='text__bold'></font>
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									<br></br>
									 <br></br>
									<br></br>
									1. <font className='text__bold'></font><br></br>
									2. <font className='text__bold'></font><br></br>
									3. <font className='text__bold'></font>
								</span>
						},{
							image: GetStartedImage3,
							headerText:
								<div>
									<span className='dark__blue'>3. </span>
									<span className='blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'></span>,
							bodyText: 
								<span className='dark__blue'>
									<br></br>
									 <br></br>
									<font className='text__bold'>,</font> <font className='text__bold'>,</font> <font className='text__bold'>,</font><br></br>
									<font className='text__bold'></font> <br></br>
									 
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									 <br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'></font> <br></br>
									 
								</span>
						},{
							image: GetStartedImage4,
							headerText:
								<div>
									<span className='dark__blue'>4.</span>
									<span className='blue'></span>
									<span className='dark__blue'></span>
									<span className='blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>  </span>,
							bodyText: 
								<span className='dark__blue'>
									 <font className='text__bold'></font><br></br>
									 <br></br>
									( 2-3 )
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									<font className='text__bold'></font><br></br>
									<br></br>
									<br></br>
									( 2-3 )
								</span>
						}]
					}
					
				/>
				<WhyChooseUs/>
				<ContactUs/>
				<AboutUs/>
			</div>
		);
	}
}
