import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import GetStarted from '../components/GetStarted';
import ExpansionPanel from '../components/ExpansionPanel';
import GetStartedImage1 from '../assets/img/content/get_start_6.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/refinance.png';
import WarningImage from '../assets/img/warning_icon.svg';
import MediaQuery from 'react-responsive';

export default class Refinance extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		const summaryText01 = <span>?</span>

		const summaryText02 = <span>?</span>

		const detailText01 = 
			<div style={{display: 'flex', flexDirection: 'column'}}>
				<span className='dark__blue'> </span>
				<span className='dark__blue'></span>
				<span className='dark__blue'> </span>
				<span className='dark__blue'>() </span>
				<span className='dark__blue'></span>
			</div>

		const detailText02 = 
			<div style={{display: 'flex', flexDirection: 'column'}}>
				<span className='dark__blue'>  <font style={{ fontWeight: 'bold'}}></font>  </span>
				<span className='dark__blue'>  2004/2547</span>
				<span className='dark__blue'> <font style={{ fontWeight: 'bold'}}></font> 2 </span>
			</div>

		return (
			<div className='container'>
				<Header
					slot={
						<div>
							<div className='largest__text'>
								<div>
									<span className='dark__blue'></span>
									<span className='blue'></span>
								</div>
							</div>

							<img src={HeaderImage} alt='Header' className='header__image'/>
						</div>
					}
				/>

				<MediaQuery maxWidth={768}>
					<ExpansionPanel
						summary={summaryText01}
						detail={detailText01}
					/>
					<ExpansionPanel
						summary={summaryText02}
						detail={detailText02}
					/>
				</MediaQuery>

				<MediaQuery minWidth={769}>
					<div className='duo__text__container'>
						<div className='left__text__container' style={{alignItems: 'flex-end'}}>
							<div>
								<div className='large__text dark__blue'>
									{summaryText01}
								</div>
								<div className='medium__text dark__blue detail__text' style={{marginTop: 30}}>
									{detailText01}
								</div>
							</div>
						</div>
						<div className='right__text__container'>
							<div className='large__text dark__blue'>
								{summaryText02}
							</div>
							<div className='medium__text dark__blue detail__text' style={{marginTop: 30}}>
								{detailText02}
							</div>
						</div>
					</div>
				</MediaQuery>

				<GetStarted
					getStartedData={
						[{
							image: GetStartedImage1,
							headerText:
								<div>
									<span className='dark__blue'>1. </span>
									<span className='blue'>?</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'> ?</span>,
							bodyText: 
								<span className='dark__blue'>
									  <font style={{ fontWeight: 'bold'}}></font> <br></br>
									 <font style={{ fontWeight: 'bold'}}> 2004/2547</font><br></br>
									 <font style={{ fontWeight: 'bold'}}></font> 2 
								</span>,
						},{
							image: GetStartedImage2,
							headerText:
								<div>
									<span className='dark__blue'>2.</span>
									<span className='blue'></span>
									<span className='dark__blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'></span>,
							bodyText: 
								<span className='dark__blue'>
									<br></br>
									   <br></br>
									<br></br>
									<font className='text__bold'></font>, <font className='text__bold'></font>, <font className='text__bold'></font>
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									<br></br>
									 <br></br>
									<br></br>
									1. <font className='text__bold'></font><br></br>
									2. <font className='text__bold'></font><br></br>
									3. <font className='text__bold'></font>
								</span>
						},{
							image: GetStartedImage3,
							headerText:
								<div>
									<span className='dark__blue'>3. </span>
									<span className='blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'></span>,
							bodyText: 
								<span className='dark__blue'>
									<br></br>
									 <br></br>
									<font className='text__bold'>,</font> <font className='text__bold'>,</font> <font className='text__bold'>,</font><br></br>
									<font className='text__bold'></font> <br></br>
									 
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									 <br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'>,</font><br></br>
									<font className='text__bold'></font> <br></br>
									 
								</span>
						},{
							image: GetStartedImage4,
							headerText:
								<div>
									<span className='dark__blue'>4.</span>
									<span className='blue'></span>
									<span className='dark__blue'></span>
									<span className='blue'></span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>  </span>,
							bodyText: 
								<span className='dark__blue'>
									 <font className='text__bold'></font><br></br>
									 <br></br>
									( 2-3 )
								</span>,
							bodyTextMobile: 
								<span className='dark__blue'>
									<br></br>
									<font className='text__bold'></font><br></br>
									<br></br>
									<br></br>
									( 2-3 )
								</span>
						}]
					}
					
				/>
				<WhyChooseUs/>
				<ContactUs 
					headerText={<span className='dark__blue'> CAR ATM</span>}
					warning={
						<span className='warning__text__large'>
							<img src={WarningImage} alt='warning' style={{height: '50%', marginRight: 16}}/>
							<div style={{display: 'flex', flexDirection: 'column'}}>
								<span>     2004 (2547)</span>
								<span> </span>
								<span> </span>
							</div>
						</span>
					}
				/>
				<AboutUs/>
			</div>
		);
	}
}